import React from "react";
import ProductsList from "../Content/ProductsList";

export default function MainTabs({ clickHandler }) {
  return (
    <div className="MainTabs">
      <button
        onClick={() =>
          clickHandler(<ProductsList />)
        }
      >
        DLA NIEJ
      </button>
      <button
        onClick={() =>
          clickHandler(<ProductsList />)
        }
      >
        DLA NIEGO
      </button>
    </div>
  );
}
